import React from 'react';
import { render, fireEvent, act } from '@testing-library/react-native';
import { Animated } from 'react-native';
import CategorySelector, { areEqual } from '../../../src/components/exercise-common/CategorySelector';

// Mock des styles pour éviter les erreurs de style pendant les tests
jest.mock('../../../src/components/exercise-common/CategorySelector/style', () => {
  const { StyleSheet } = jest.requireActual('react-native');
  return () => StyleSheet.create({
    container: {},
    backgroundGradient: {},
    scrollView: {},
    scrollContent: {},
    categoryItemWrapper: {},
    categoryTouchable: {},
    selectedCategoryItem: {},
    selectedInner: {},
    selectedCategoryText: { color: '#FFFFFF' },
    sparkleContainer: {},
    sparkle: {},
    categoryItem: {},
    categoryInner: {},
    categoryText: {},
  });
});

describe('CategorySelector', () => {
  const categories = [
    { id: '1', name: 'Grammar' },
    { id: '2', name: 'Vocabulary' },
    { id: '3', name: 'Reading' },
  ];
  const onSelectCategoryMock = jest.fn();

  beforeEach(() => {
    jest.useFakeTimers();
    jest.clearAllMocks();
  });

  afterEach(() => {
    jest.useRealTimers();
    jest.restoreAllMocks();
  });

  // Tests de rendu et d'interaction de base

  it('should render all categories and select one correctly', () => {
    const { getByText } = render(
      <CategorySelector categories={categories} selectedCategory="2" onSelectCategory={onSelectCategoryMock} />
    );
    
    expect(getByText('Grammar')).toBeTruthy();
    expect(getByText('Vocabulary')).toBeTruthy();
    expect(getByText('Reading')).toBeTruthy();
    expect(getByText('✨')).toBeTruthy();

    fireEvent.press(getByText('Grammar'));
    expect(onSelectCategoryMock).toHaveBeenCalledWith('1');
    expect(onSelectCategoryMock).not.toHaveBeenCalledWith('2');
  });
  
  // Tests de la logique d'animation

  it('should trigger animation when selectedCategory changes', async () => {
    const mockTiming = jest.spyOn(Animated, 'timing');
    const { rerender } = render(
      <CategorySelector categories={categories} selectedCategory="1" onSelectCategory={onSelectCategoryMock} />
    );
    mockTiming.mockClear();
    
    await act(async () => {
      rerender(<CategorySelector categories={categories} selectedCategory="2" onSelectCategory={onSelectCategoryMock} />);
      jest.advanceTimersByTime(300);
    });

    expect(mockTiming).toHaveBeenCalledTimes(2);
    expect(mockTiming).toHaveBeenCalledWith(
      expect.anything(),
      expect.objectContaining({ toValue: 0, duration: 200 })
    );
    expect(mockTiming).toHaveBeenCalledWith(
      expect.anything(),
      expect.objectContaining({ toValue: 1, duration: 300 })
    );
  });

  it('should not call onSelectCategory if an animation is in progress', async () => {
    const { rerender, getByText } = render(
      <CategorySelector categories={categories} selectedCategory="1" onSelectCategory={onSelectCategoryMock} />
    );
    
    await act(async () => {
      rerender(<CategorySelector categories={categories} selectedCategory="2" onSelectCategory={onSelectCategoryMock} />);
      jest.advanceTimersByTime(100);
    });
    
    fireEvent.press(getByText('Reading'));
    expect(onSelectCategoryMock).not.toHaveBeenCalled();
    expect(onSelectCategoryMock).not.toHaveBeenCalledWith('3');
  });

  // Tests de la logique avancée et de la mémorisation

  it('should handle new categories correctly and rerender the new list', () => {
    const { rerender, getByText, queryByText } = render(
      <CategorySelector categories={categories} selectedCategory="1" />
    );

    const newCategories = [{ id: '4', name: 'Writing' }, ...categories];
    rerender(<CategorySelector categories={newCategories} selectedCategory="1" />);
    
    expect(getByText('Writing')).toBeTruthy();
    expect(getByText('Grammar')).toBeTruthy();

    const updatedCategories = [{ id: '1', name: 'Grammar' }];
    rerender(<CategorySelector categories={updatedCategories} selectedCategory="1" />);
    expect(queryByText('Vocabulary')).toBeNull();
  });
  
  it('should scroll to the selected category on layout', async () => {
    // Création des mocks spécifiques à ce test
    const mockScrollTo = jest.fn();
    const mockRef = { current: { scrollTo: mockScrollTo } };

    // Mock `useRef` pour qu'il retourne notre mockRef seulement pour le premier appel
    jest.spyOn(React, 'useRef').mockReturnValueOnce(mockRef);

    const { getByText, rerender } = render(
      <CategorySelector categories={categories} selectedCategory="1" />
    );
    
    expect(mockScrollTo).not.toHaveBeenCalled();

    rerender(<CategorySelector categories={categories} selectedCategory="2" />);
    
    fireEvent(getByText('Vocabulary'), 'layout', {
      nativeEvent: {
        layout: { x: 100, y: 0, width: 80, height: 30 },
      },
    });

    await act(async () => {
      jest.runAllTimers();
    });

    expect(mockScrollTo).toHaveBeenCalledWith({ x: 60, y: 0, animated: true });
  });

  it('should correctly memoize the component based on prop changes', () => {
    const prevProps = { categories: [{ id: '1', name: 'Grammar' }], selectedCategory: '1', primaryColor: '#abc' };
    const nextPropsSame = { categories: [{ id: '1', name: 'Grammar' }], selectedCategory: '1', primaryColor: '#abc' };
    const nextPropsDifferentCat = { categories: [{ id: '2', name: 'Vocabulary' }], selectedCategory: '1', primaryColor: '#abc' };
    const nextPropsDifferentSel = { categories: [{ id: '1', name: 'Grammar' }], selectedCategory: '2', primaryColor: '#abc' };
    const nextPropsDifferentColor = { categories: [{ id: '1', name: 'Grammar' }], selectedCategory: '1', primaryColor: '#def' };
    
    expect(areEqual(prevProps, nextPropsSame)).toBe(true);
    expect(areEqual(prevProps, nextPropsDifferentCat)).toBe(false);
    expect(areEqual(prevProps, nextPropsDifferentSel)).toBe(false);
    expect(areEqual(prevProps, nextPropsDifferentColor)).toBe(false);
  });
});